import React, { Component } from 'react'
import '../css/ShortForecastContainer.css';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faBolt, faCloud, faCloudMoon, faCloudMoonRain, faCloudShowersHeavy, 
        faCloudRain, faCloudSun, faCloudSunRain, faIcicles, faMoon, faRainbow, faSmog, 
        faSnowflake, faSun, faWind,  } from '@fortawesome/free-solid-svg-icons'

export default class ShortForecastContainer extends Component {
  constructor(props) {
    super(props)

    this.getWeatherIcon = this.getWeatherIcon.bind(this)
  }

  getWeatherIcon(){
    const fc = this.props.forecast.toLowerCase()

    // The following conditions are derived from the document at https://www.weatherapi.com/docs/weather_conditions.json
    // Day
    if (this.props.is_day) {
      if (fc === "sunny") return <FontAwesomeIcon icon={faSun} />
      if (fc === "mostly sunny") return <FontAwesomeIcon icon={faSun} />
      if (fc === "partly cloudy") return <FontAwesomeIcon icon={faCloudSun} />
      if (fc === "cloudy") return <FontAwesomeIcon icon={faCloud} />
      if (fc === "overcast") return <FontAwesomeIcon icon={faCloud} />
      if (fc === "mist") return <FontAwesomeIcon icon={faCloud} />
      if (fc === "patchy rain possible") return <FontAwesomeIcon icon={faCloudSunRain} />
      if (fc === "patchy snow possible")return <FontAwesomeIcon icon={faSnowflake} />
      if (fc === "patchy sleet possible")return <FontAwesomeIcon icon={faSnowflake} />
      if (fc === "patchy freezing drizzle possible")return <FontAwesomeIcon icon={faIcicles} />
      if (fc === "thundery outbreaks possible") return <FontAwesomeIcon icon={faBolt} />
      if (fc === "blowing snow")return <FontAwesomeIcon icon={faSnowflake} />
      if (fc === "blizzard")return <FontAwesomeIcon icon={faSnowflake} />
      if (fc === "fog") return <FontAwesomeIcon icon={faSmog} />
      if (fc === "areas of fog") return <FontAwesomeIcon icon={faSun} />
      if (fc === "freezing fog") return <FontAwesomeIcon icon={faSmog} />
      if (fc === "patchy light drizzle") return <FontAwesomeIcon icon={faCloudRain} />
      if (fc === "light drizzle") return <FontAwesomeIcon icon={faCloudRain} />
      if (fc === "freezing drizzle") return <FontAwesomeIcon icon={faIcicles} />
      if (fc === "heavy freezing drizzle") return <FontAwesomeIcon icon={faIcicles} />
      if (fc === "patchy light rain") return <FontAwesomeIcon icon={faCloudSunRain} />
      if (fc === "light rain") return <FontAwesomeIcon icon={faCloudSunRain} />
      if (fc === "moderate rain at times") return <FontAwesomeIcon icon={faCloudRain} />
      if (fc === "moderate rain") return <FontAwesomeIcon icon={faCloudShowersHeavy} />
      if (fc === "heavy rain at times") return <FontAwesomeIcon icon={faCloudShowersHeavy} />
      if (fc === "heavy rain") return <FontAwesomeIcon icon={faCloudShowersHeavy} />
      if (fc === "light freezing rain") return <FontAwesomeIcon icon={faIcicles} />
      if (fc === "moderate or heavy freezing rain") return <FontAwesomeIcon icon={faIcicles} />
      if (fc === "light sleet") return <FontAwesomeIcon icon={faSnowflake} />
      if (fc === "moderate or heavy sleet") return <FontAwesomeIcon icon={faSnowflake} />
      if (fc === "patchy light snow") return <FontAwesomeIcon icon={faSnowflake} />
      if (fc === "light snow") return <FontAwesomeIcon icon={faSnowflake} />
      if (fc === "patchy moderate snow") return <FontAwesomeIcon icon={faSnowflake} />
      if (fc === "moderate snow") return <FontAwesomeIcon icon={faSnowflake} />
      if (fc === "patchy heavy snow") return <FontAwesomeIcon icon={faSnowflake} />
      if (fc === "heavy snow") return <FontAwesomeIcon icon={faSnowflake} />
      if (fc === "ice pellets") return <FontAwesomeIcon icon={faIcicles} />
      if (fc === "light rain shower") return <FontAwesomeIcon icon={faCloudSunRain} />
      if (fc === "moderate or heavy rain shower") return <FontAwesomeIcon icon={faCloudShowersHeavy} />
      if (fc === "torrential rain shower") return <FontAwesomeIcon icon={faCloudShowersHeavy} />
      if (fc === "light sleet showers") return <FontAwesomeIcon icon={faSnowflake} />
      if (fc === "moderate or heavy sleet showers") return <FontAwesomeIcon icon={faSnowflake} />
      if (fc === "light snow showers") return <FontAwesomeIcon icon={faSnowflake} />
      if (fc === "moderate or heavy snow showers") return <FontAwesomeIcon icon={faSnowflake} />
      if (fc === "light showers of ice pellets") return <FontAwesomeIcon icon={faIcicles} />
      if (fc === "moderate or heavy showers of ice pellets") return <FontAwesomeIcon icon={faIcicles} />
      if (fc === "patchy light rain with thunder") return <FontAwesomeIcon icon={faBolt} />
      if (fc === "moderate or heavy rain with thunder") return <FontAwesomeIcon icon={faBolt} />
      if (fc === "patchy light snow with thunder") return <FontAwesomeIcon icon={faSnowflake} />
      if (fc === "moderate or heavy snow with thunder") return <FontAwesomeIcon icon={faSnowflake} />
    }


    // Night
    if (!this.props.is_day){
      if (fc === "clear") return <FontAwesomeIcon icon={faMoon} />
      if (fc === "partly clear") return <FontAwesomeIcon icon={faMoon} />
      if (fc === "mostly clear") return <FontAwesomeIcon icon={faMoon} />
      if (fc === "partly cloudy") return <FontAwesomeIcon icon={faCloudMoon} />
      if (fc === "cloudy") return <FontAwesomeIcon icon={faCloud} />
      if (fc === "overcast") return <FontAwesomeIcon icon={faCloud} />
      if (fc === "mist") return <FontAwesomeIcon icon={faCloud} />
      if (fc === "patchy rain possible") return <FontAwesomeIcon icon={faCloudMoonRain} />
      if (fc === "patchy snow possible")return <FontAwesomeIcon icon={faSnowflake} />
      if (fc === "patchy sleet possible")return <FontAwesomeIcon icon={faSnowflake} />
      if (fc === "patchy freezing drizzle possible")return <FontAwesomeIcon icon={faIcicles} />
      if (fc === "thundery outbreaks possible") return <FontAwesomeIcon icon={faBolt} />
      if (fc === "blowing snow")return <FontAwesomeIcon icon={faSnowflake} />
      if (fc === "blizzard")return <FontAwesomeIcon icon={faSnowflake} />
      if (fc === "fog") return <FontAwesomeIcon icon={faSmog} />
      if (fc === "areas of fog") return <FontAwesomeIcon icon={faMoon} />
      if (fc === "freezing fog") return <FontAwesomeIcon icon={faSmog} />
      if (fc === "patchy light drizzle") return <FontAwesomeIcon icon={faCloudRain} />
      if (fc === "light drizzle") return <FontAwesomeIcon icon={faCloudRain} />
      if (fc === "freezing drizzle") return <FontAwesomeIcon icon={faIcicles} />
      if (fc === "heavy freezing drizzle") return <FontAwesomeIcon icon={faIcicles} />
      if (fc === "patchy light rain") return <FontAwesomeIcon icon={faCloudMoonRain} />
      if (fc === "light rain") return <FontAwesomeIcon icon={faCloudMoonRain} />
      if (fc === "moderate rain at times") return <FontAwesomeIcon icon={faCloudRain} />
      if (fc === "moderate rain") return <FontAwesomeIcon icon={faCloudShowersHeavy} />
      if (fc === "heavy rain at times") return <FontAwesomeIcon icon={faCloudShowersHeavy} />
      if (fc === "heavy rain") return <FontAwesomeIcon icon={faCloudShowersHeavy} />
      if (fc === "light freezing rain") return <FontAwesomeIcon icon={faIcicles} />
      if (fc === "moderate or heavy freezing rain") return <FontAwesomeIcon icon={faIcicles} />
      if (fc === "light sleet") return <FontAwesomeIcon icon={faSnowflake} />
      if (fc === "moderate or heavy sleet") return <FontAwesomeIcon icon={faSnowflake} />
      if (fc === "patchy light snow") return <FontAwesomeIcon icon={faSnowflake} />
      if (fc === "light snow") return <FontAwesomeIcon icon={faSnowflake} />
      if (fc === "patchy moderate snow") return <FontAwesomeIcon icon={faSnowflake} />
      if (fc === "moderate snow") return <FontAwesomeIcon icon={faSnowflake} />
      if (fc === "patchy heavy snow") return <FontAwesomeIcon icon={faSnowflake} />
      if (fc === "heavy snow") return <FontAwesomeIcon icon={faSnowflake} />
      if (fc === "ice pellets") return <FontAwesomeIcon icon={faIcicles} />
      if (fc === "light rain shower") return <FontAwesomeIcon icon={faCloudMoonRain} />
      if (fc === "moderate or heavy rain shower") return <FontAwesomeIcon icon={faCloudShowersHeavy} />
      if (fc === "torrential rain shower") return <FontAwesomeIcon icon={faCloudShowersHeavy} />
      if (fc === "light sleet showers") return <FontAwesomeIcon icon={faSnowflake} />
      if (fc === "moderate or heavy sleet showers") return <FontAwesomeIcon icon={faSnowflake} />
      if (fc === "light snow showers") return <FontAwesomeIcon icon={faSnowflake} />
      if (fc === "moderate or heavy snow showers") return <FontAwesomeIcon icon={faSnowflake} />
      if (fc === "light showers of ice pellets") return <FontAwesomeIcon icon={faIcicles} />
      if (fc === "moderate or heavy showers of ice pellets") return <FontAwesomeIcon icon={faIcicles} />
      if (fc === "patchy light rain with thunder") return <FontAwesomeIcon icon={faBolt} />
      if (fc === "moderate or heavy rain with thunder") return <FontAwesomeIcon icon={faBolt} />
      if (fc === "patchy light snow with thunder") return <FontAwesomeIcon icon={faSnowflake} />
      if (fc === "moderate or heavy snow with thunder") return <FontAwesomeIcon icon={faSnowflake} />
    }

  // Fallback
  if (fc.includes("snow")) return <FontAwesomeIcon icon={faSnowflake} />
  if (fc.includes("thunder")) return <FontAwesomeIcon icon={faBolt} />
  if (fc.includes("smoke")) return <FontAwesomeIcon icon={faSmog} />
  if (fc.includes("smog")) return <FontAwesomeIcon icon={faSmog} />
  if (fc.includes("wind")) return <FontAwesomeIcon icon={faWind} />
  return <FontAwesomeIcon icon={faRainbow} />
  }

  render() { 
    if (this.props.forecast.toLowerCase() !== "") {
      return (
        <div className="ShortForecastContainer">
          <span className="ForecastIcon" style={{color: this.props.css_style.font_color, filter:`drop-shadow(3px 2px 12px black)`}}>{this.getWeatherIcon()}</span>
          <span className="ShortForecastText" style={{color: this.props.css_style.font_color}}>{this.props.forecast}</span>
        </div>
      )
    } else {
      return (<div></div>)
    }
  }
}
